/*全局样式*/
html body {
    background-color: #ffffff;
    padding: 0;
    margin: 0;
}

.container_Box {
    display: flex;
    height: 100vh;
    overflow: hidden;
    padding: 0;
    margin: 0;
    background-color: #f5f5f5;
}

.main ol, .main ul {
    padding-left: 12px;
}

.main ul li, .main ol li {
    margin: 4px 12px 4px;
}

.container_Box .one ul > li div h4, .container_Box .two ul > li div h4 {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.one .list {
    cursor: pointer;
}

.active {
    border-radius: 8px;
    background-color: #ff9f62;
}

#activeFont {
    color: #fffef6;
}

#commonFont {
    color: #222222;
}

.container_Box .two, .container_Box .one {
    display: inline-block;
    height: 100vh;
    overflow-y: scroll;
    transition: width .1s linear;
    position: relative;
    margin-top: 10px;
}

.container_Box .one {
    width: 15vw;
    padding: 20px;
    background-color: #ffffff;
}

.container_Box .two {
    width: 0;
    padding: 0;
    margin-left: 5px;
    background-color: #ffffff;

}

.container_Box .main {
    display: inline-block;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    position: relative;
    background-color: #f5f5f5;
}

.title {
    cursor: pointer;
}

/*公共样式*/
.ant-spin-container .ant-list-items .ant-list-item {
    padding: 6px;
}

.ant-list .ant-spin-container .ant-list-item {
    padding: 3px;
}

.ant-list-item .ant-list-item-meta .ant-list-item-meta-content .ant-list-item-meta-title {
    padding: 0;
    margin: 0;
}

/*图标*/

.layerIcon {
    position: absolute;
    left: 0;
    right: 0;
    top: 10px;
    display: flex;
}

.zoom {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 95vw;
    transition: all .3s linear;
}

.one_hide_show {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 10px;
    transition: all .3s linear;
}

.two_hide_show {
    width: 30px;
    cursor: pointer;
    z-index: 100;
    position: sticky;
    top: 15px;
    left: 45px;
    transition: all .3s linear;
}

.ReceivingCode {
    position: fixed;
    z-index: 9999;
    bottom: 20px;
    right: 10px;
    width: 120px;
    border: 10px solid #d7d7f3;
    border-radius: 8px;
    cursor: pointer;
}

.ReceivingCode img {
    width: 100%;
}

.ReceivingCode .closeQr {
    width: 25px;
    position: absolute;
    left: -15px;
    top: -15px;
}

p > code {
    background-color: #f3f3f3;
    color: #da702b;
    border-radius: 4px;
    padding: 2px;
}

blockquote {
    border-left: 8px solid #488261;
    background-color: #eef7f2;
    border-radius: 4px;
    padding: 10px 6px;
}

blockquote p {
    margin-bottom: 0;
}

.ValineContainer {
    margin: 0 5px;
    width: 75%;;
}

.Valine {
    margin-top: 0px;
}

.react-valine .v-edit-area .v-edit-control {
    text-align: left;
}

.react-valine .v-main-wrapper .v-edit-area .v-edit-submit {
    display: inline-block;
}

.Valine .react-valine .v-main-wrapper .vheader .vavatars-select-button .vavatars-edit-icon {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.5);
}

.drawer-panel-wrapper.react-valine .drawer-panel-mask {
    background: rgba(255, 255, 255, 0);
}

.drawer-panel-wrapper.react-valine .drawer-content-top {
    position: absolute;
    width: 400px;
    height: 200px;
    left: 55%;
    top: 55%;
    margin-top: -100px;
    margin-left: -200px;
    background-color: #000c17;
    border-radius: 12px;
}

.drawer-panel-wrapper.react-valine .drawer-content-bottom {
    position: absolute;
    bottom: 0;
    width: 50%;
    left: 50%;
    margin-left: -25%;
    height: auto;
    background-color: rgba(255, 254, 246, 0.9);
}

.drawer-panel-wrapper.react-valine .drawer-content-bottom .emoji-panel-box {
    font-size: 18px;
    text-align: justify;
    max-height: 80vh;
    overflow: auto;
    margin-bottom: 10px;
    box-shadow: 0 0 1px #f0f0f0;
    user-select: none;
    border: 1px solid #ccc5c6;
    border-radius: 4px;
}

.drawer-panel-wrapper.react-valine .emoji-panel-box .emoji-panel-item {
    font-style: normal;
    padding: 4px 0;
    width: 30px;
    cursor: pointer;
    text-align: center;
    display: inline-block;
    vertical-align: middle;
    transition: background .5s linear;
}

.md_container {
    background-color: #f5f5f5;
}

.md_content {
    width: 75%;
    display: inline-block;
    flex-direction: column;
    margin: 10px 5px;
    padding: 20px;
    background-color: #ffffff;
}

.md_navigation {
    position: fixed;
    right: 0;
    display: inline-block;
    top: 0;
    bottom: 0;
    width: 200px;
    background-color: #f5f5f5;
}

.md_nav_content {
    top: 10px;
    width: 200px;
    background-color: #ffffff;
    right: 10px;
    position: fixed;
    overflow-x: hidden;
    overflow-y: scroll;
    max-height: 450px;
}

::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
}

/*回到首页图标*/
.homeIcon {
    width: 30px;
    top: 10px;
    left: 85px;
    z-index: 9999;
    opacity: 0;
    position: fixed;
    transition: all .1s linear;
    cursor: pointer;
}

body:hover .homeIcon_active {
    opacity: 1;
}
